<template>
	<div id="searchResult">
		<table>
			<tr><td><img id="minSearchResultImage" :src="'https://fishbase.us/images/species/' + result.image" @error="changeImage"></td>
				<td>
					<h2>
						<a href="#" @click="getDetails">{{ result.taxonomy }}</a>
					</h2>
					<p v-if="result.commonName == ''"><i>No common name found.</i></p>
					{{ result.commonName }}
				</td>
			</tr>
		</table>
	</div>
</template>

<style>
#searchResult{
	margin-top: 10px;
}
h2{
	margin-bottom: 0px !important;
}
#minSearchResultImage{
	max-width: 200px;
	max-height: 200px;
}
table{
	width: 100%;
	background-color: #e0e1e2;
	border-radius: 5px;
}
td{
	width: 33%;
	text-align: left;
	padding-left: 0px;
	vertical-align: top;

	padding-top: 1px;
}
ul{
	margin-top: 0px;
	padding-left: 17px;
}
</style>

<script>



export default{
	name: 'searchResult',
	props: {
		result: {
			required: true
		}
	},
	data(){
		return{
			detailsPageUrl: ''
		}
	},
	methods:{
		changeImage(event){
			event.target.src = require("@/assets/no_image.png")
		},
		getDetails(){
			//change the window location to the details
			// page for a given taxonomy

			window.location.assign(this.detailsPageUrl + this.result.taxonomy.replace(" ", "_"))
		}
	},
	mounted(){
		if( window.location.href.includes("localhost") ){
			this.detailsPageUrl = "https://localhost:8081?param="
			this.mainPageUrl = "https://localhost:8080"
		}else{
			this.detailsPageUrl = "https://jacob-pugsley.github.io/SimpleFishBase_Details?param="
			this.mainPageUrl = "https://jacob-pugsley.github.io/SimpleFishBase_Main"
		}
	}
}
</script>